import index from '@/pages/index'
import map from '@/pages/map'
import lifeGame from '@/pages/lifeGame'
import calculate from '@/pages/calculate'

const route = [
  {
    path: '/',
    name: 'index',
    component: index
  },
  {
    path: '/map',
    name: 'map',
    component: map
  },
  {
    path: '/lifeGame',
    name: 'lifeGame',
    component: lifeGame
  },
  {
    path: '/calculate',
    name: 'calculate',
    component: calculate
  },
]
export default route;


